img{
    width: 200px;
    height: 200px;
    padding-left: 15px;
}

.container{
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(auto-fit, 250px);
    align-items: start;
}


.profile{
    width: fit-content;
    border-radius: 10px;
    background-color: #ffffff;
    padding:5px;
    margin: 10px;
}
.details{
    font-family: sans-serif;
}

.details h4{
    font-size: 20px;
    text-align: center;
}

.details h5{
    font-size: 18px;
    margin: 0;
    padding: 0;
}